/* App.css - simple clean chat styles */
.app-root {
  font-family: Inter, Arial, Helvetica, sans-serif;
  display: flex;
  min-height: 100vh;
  align-items: center;
  justify-content: center;
  background: #f2f4f8;
}

.chat-container {
  width: 420px;
  max-width: calc(100% - 40px);
  background: #ffffff;
  border-radius: 12px;
  box-shadow: 0 6px 18px rgba(20, 30, 50, 0.08);
  overflow: hidden;
  display: flex;
  flex-direction: column;
}

.chat-header {
  padding: 16px 18px;
  border-bottom: 1px solid #eef0f4;
  background: linear-gradient(90deg, #2b8ae2, #0666c3);
  color: #fff;
}

.chat-header h2 {
  margin: 0;
  font-size: 18px;
  font-weight: 600;
}

.chat-window {
  padding: 16px;
  height: 450px;
  overflow-y: auto;
  display: flex;
  flex-direction: column;
  gap: 8px;
  background: linear-gradient(180deg, #fbfdff, #f7fbff);
}

.chat-bubble {
  max-width: 78%;
  padding: 10px 12px;
  border-radius: 14px;
  line-height: 1.3;
  word-break: break-word;
  box-shadow: 0 1px 2px rgba(16, 24, 40, 0.04);
}

.chat-bubble.user {
  align-self: flex-end;
  background: #d2ecff;
  color: #022c43;
  border-bottom-right-radius: 6px;
}

.chat-bubble.bot {
  align-self: flex-start;
  background: #f1f4f8;
  color: #04101a;
  border-bottom-left-radius: 6px;
}

.composer {
  display: flex;
  gap: 10px;
  padding: 12px;
  border-top: 1px solid #eef0f4;
}

.composer input {
  flex: 1;
  padding: 10px 12px;
  border: 1px solid #e6ecf2;
  border-radius: 8px;
  outline: none;
  font-size: 14px;
}

.composer button {
  padding: 10px 16px;
  background: #1173d6;
  color: white;
  border: none;
  border-radius: 8px;
  cursor: pointer;
  font-weight: 600;
}

.composer button:disabled {
  background: #9fbfe6;
  cursor: default;
}
